export const TOGGLE_IMAGE_FILTER = 'TOGGLE_IMAGE_FILTER'
export function toggleImageFilter () {
  return {
    type: TOGGLE_IMAGE_FILTER
  }
}

export const TOGGLE_AUDIO_FILTER = 'TOGGLE_AUDIO_FILTER'
export function toggleAudioFilter () {
  return {
    type: TOGGLE_AUDIO_FILTER
  }
}



